.showcase-pro {
  // .showcase-pro__content
  margin-bottom: 72px;

  @include tablet {
    margin-bottom: 36px;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 18px;
    @include tablet {
      grid-template-columns: 1fr;
    }
  }
  &__content-item-link {
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
    text-align: center;
    justify-content: center;
    color: #000;
    max-width: 238px;
    padding: 24px 36px;
    margin: 0;
    transition: 0.2s ease-in;
    @include tablet {
      display: flex;
      width: 100%;
      max-width: unset;
    }
    background-color: #fce600;
    border: 1px solid transparent;
    &:hover {
      opacity: 0.8;
      border: 1px solid #fce600;
    }
  }
  // .showcase-pro__content-item
  &__content-item-bg {
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    border-radius: 36px;
    pointer-events: none;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__content-item {
    overflow: hidden;
    position: relative;
    &.blue {
      grid-column: span 3;
      display: flex;
      justify-content: space-between;
      gap: 10px;
      border-radius: 36px;
      @include tablet {
        grid-column: span 1;
      }
      .showcase-pro__content-item-text {
        max-width: 385px;
        @include tablet {
          padding-right: 30px;
          max-width: unset;
        }
      }
      .showcase-pro__content-item-title {
        font-weight: 500;
        font-size: 32px;
        line-height: 145%;
        color: #fff;
      }
      .showcase-pro__content-item-desc {
        font-weight: 300;
        font-size: 18px;
        line-height: 145%;
        color: #fff;
        margin-bottom: 16px;
        @include tablet {
          margin-bottom: 31px;
        }
      }
      .showcase-pro__content-item-bg {
        mix-blend-mode: luminosity;
        opacity: 1;
      }
      .showcase-pro__content-item-wrapper {
        overflow: hidden;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 0 138px 0 90px;
        border-radius: 36px;
        background-color: #0419fd;
        @include tablet {
          flex-direction: column;

          padding: 30px 0 0 30px;
        }
      }
      .showcase-pro__content-item-img {
        position: relative;
        @include tablet {
          align-self: flex-end;
        }
        img {
          @include tablet {
            opacity: 0.55;
          }
        }
      }
    }
    &.pink {
      grid-column: span 1;
      @include tablet {
        grid-column: span 1;
      }
      .showcase-pro__content-item-bg {
        mix-blend-mode: luminosity;
        opacity: 1;
        top: 0;
      }
      .showcase-pro__content-item-wrapper {
        overflow: hidden;
        display: flex;
        justify-content: center;
        flex-direction: column;
        gap: 30px;
        align-items: center;
        width: 100%;
        height: 100%;
        border-radius: 36px;
        padding: 55px 10px;
        border-radius: 36px;
        background-color: #fc0f79;
      }
      .showcase-pro__content-item-title {
        font-weight: 600;
        font-size: 18px;
        line-height: 125%;
        text-transform: uppercase;
        text-align: center;
        color: #fff;
        max-width: 251px;
        margin: 0 auto;
      }
      .showcase-pro__content-item-desc {
        font-weight: 300;
        font-size: 14px;
        line-height: 175%;
        text-align: center;
        color: #fff;
        max-width: 283px;
        margin: 0 auto;
        span {
          font-weight: 700;
        }
      }
    }
    &.yellow {
      grid-column: span 2;
      @include tablet {
        grid-column: span 1;
      }
      .showcase-pro__content-item-img {
        position: absolute;
        right: 25px;
        top: 0px;
        @include tablet {
          right: 50px;
          position: relative;
        }
      }
      .showcase-pro__content-item-img img {
        mix-blend-mode: normal;
        position: relative;
        z-index: 0;
        max-width: 481px;
      }
      .showcase-pro__content-item-wrapper {
        height: 100%;
        overflow: hidden;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 0 25px 0 70px;
        border-radius: 36px;
        background: #fce600;
        @include tablet {
          flex-direction: column;
          padding: 54px 25px 30px 27px;
        }
      }
      .showcase-pro__content-item-text {
        position: relative;
        z-index: 1;
      }
      .showcase-pro__content-item-title {
        font-weight: 700;
        font-size: 30px;
        line-height: 125%;
        color: #000;
      }
      .showcase-pro__content-item-desc {
        font-weight: 300;
        font-size: 18px;
        line-height: 165%;
        color: #000;
        max-width: 385px;
        span {
          font-weight: 700;
          color: #fff;
        }
      }
    }
  }

  // .showcase-pro__content-item-text

  &__content-item-text {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  // .showcase-pro__content-item-title

  &__content-item-title {
    margin: 0;
    @include small-tablet {
      word-break: break-all;
    }
  }

  // .showcase-pro__content-item-desc

  &__content-item-desc {
    margin: 0;
  }

  // .showcase-pro__content-item-img

  &__content-item-img {
    img {
      mix-blend-mode: lighten;
    }
  }

  // .showcase-pro__content-item-improve

  &__content-item-improve {
    position: absolute;

    display: flex;
    align-items: center;
    gap: 11px;
    &.first {
      top: 81px;
      left: -243px;
      max-width: 275px;
      .showcase-pro__content-item-improve-desc {
        span {
          font-weight: 700;
        }
        font-weight: 300;
      }
      @include tablet {
        left: 0;
        top: 25%;
      }
    }
    &.second {
      bottom: 81px;
      left: -140px;
      max-width: 244px;

      @include tablet {
        left: 41px;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    &.third {
      top: 63px;
      right: -117px;
      max-width: 200px;

      @include tablet {
        left: 0;
        bottom: 25%;
        top: unset;
      }
    }
  }

  // .showcase-pro__content-item-improve-num

  &__content-item-improve-num {
    font-weight: 900;
    font-size: 45px;
    line-height: 130%;
    color: rgba(255, 255, 255, 0.24);
  }

  // .showcase-pro__content-item-improve-desc

  &__content-item-improve-desc {
    font-weight: 500;
    font-size: 15px;
    line-height: 130%;
    color: #fff;
  }

  // .showcase-pro__content-item-cards

  &__content-item-cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 12px;
    width: 100%;
    max-width: 283px;
    margin: 0 auto;
  }

  // .showcase-pro__content-item-card

  &__content-item-card {
    border-radius: 24px;
    display: flex;
    flex-direction: column;
    gap: 8px;
    padding: 12px;
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
    text-transform: uppercase;

    img {
      max-width: 36px;
    }
    &:first-child {
      color: #000;
      background: rgba(255, 255, 255, 0.45);
      backdrop-filter: blur(15px);
    }
    &:last-child {
      background: #fff;
      transform: rotate(7deg);
      opacity: 0.9;
      color: #fd62a7;
    }
  }

  // .showcase-pro__content-item-card-text

  &__content-item-card-text {
    display: flex;
    flex-direction: column;
    gap: 4px;
    margin-bottom: 8px;
    .dark {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 13px;
      line-height: 125%;
      color: rgba(0, 0, 0, 0.68);
    }
    .gray {
      font-weight: 400;
      font-size: 9px;
      line-height: 125%;
      color: rgba(0, 0, 0, 0.35);
    }
  }

  // .showcase-pro__content-item-card-value

  &__content-item-card-value {
    display: flex;
    flex-direction: column;
    gap: 4px;
    width: 100%;
    &.showup {
      .showcase-pro__content-item-card-bar {
        background: linear-gradient(
          90deg,
          rgba(252, 15, 121, 1) 80%,
          rgba(37, 37, 42, 0.3127626050420168) 80%
        );
      }
    }
  }

  // .showcase-pro__content-item-card-num

  &__content-item-card-num {
    font-weight: 500;
    font-size: 16px;
    line-height: 110%;
    text-align: center;
    color: #fc0f79;
  }

  // .showcase-pro__content-item-card-bar

  &__content-item-card-bar {
    width: 100%;
    background: linear-gradient(
      90deg,
      rgba(252, 15, 121, 1) 20%,
      rgba(37, 37, 42, 0.3127626050420168) 20%
    );
    height: 4px;
    border-radius: 24px;
  }

  // .showcase-pro__content-item-card-bar-progress

  &__content-item-card-bar-progress {
  }
}

.uniqueness-pro {
  // .uniqueness-pro__wrapper
  margin-top: -1px;

  @include tablet {
    margin-right: -16px;
  } // .uniqueness__wrapper
  &__swiper-inner-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 34px 0;
    @include mobile {
      padding: 24px 0;
    }
    img {
      max-width: 256px;
      @include mobile {
        max-width: 192px;
      }
    }
  }
  &__wrapper {
    overflow: hidden;
    background: #000;
  }
  &__swiper-inner {
    .swiper-wrapper {
      max-height: 569px;
      @include mobile {
        max-height: 425px;
      }
    }
  }
  // .uniqueness-pro__scroll

  &__scroll {
    @include tablet {
      display: none;
    }
    padding: 72px 0;
    @include tablet {
      padding: 36px 0;
    }
    display: flex;
    gap: 100px;
    padding-bottom: 54px;
    &::-webkit-scrollbar {
      border-radius: 5000px;
      background: rgba(#102048, 0.14);
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 5000px;
      background-color: rgba($color: #102048, $alpha: 1);
    }
  }

  // .uniqueness-pro__scroll-item

  &__scroll-item {
    flex: 0 0 100%;
    img {
      width: 100%;
    }
  }

  // .uniqueness-pro__scroll-item-upper

  &__scroll-item-upper {
    display: flex;
    flex-direction: column;
    gap: 18px;

    margin-bottom: 130px;
    @include tablet {
      transition: 0.2s ease-in;
      transform: translateX(-300px);
      opacity: 0;
      margin-bottom: 36px;
      gap: 8px;
    }
  }

  // .uniqueness-pro__scroll-item-title

  &__scroll-item-title {
    font-weight: 700;
    font-size: 64px;
    line-height: 110%;
    text-align: center;
    color: #fff;
    margin: 0 auto;
    @include tablet {
      font-weight: 700;
      font-size: 30px;
      line-height: 110%;
      text-align: center;
      color: #fff;
    }
  }

  // .uniqueness-pro__scroll-item-subtitle

  &__scroll-item-subtitle {
    font-weight: 300;
    font-size: 24px;
    line-height: 165%;
    text-align: center;
    max-width: 806px;
    margin: 0 auto;
    color: #fff;
    opacity: 0.75;
    @include tablet {
      max-width: 289px;
      margin: 0 auto;
      font-weight: 300;
      font-size: 14px;
      line-height: 165%;
      text-align: center;
      color: #fff;
    }
  }

  // .uniqueness-pro__scroll-item-content

  &__scroll-item-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 72px;
    align-items: center;
    @include tablet {
      grid-template-columns: 1fr;
      gap: 12px;
    }
  }

  // .uniqueness-pro__scroll-item-img

  &__scroll-item-img {
    width: 100%;
    position: relative;
    max-width: 547px;
    margin: 0 auto;
    border-radius: 36px;

    @include mobile {
      max-width: 366px;
      margin: 0 auto;
    }
  }

  // .uniqueness-pro__scroll-item-text

  &__scroll-item-text {
    display: flex;
    flex-direction: column;
    @include tablet {
      max-width: 366px;
      margin: 0 auto;
    }
  }

  // .uniqueness-pro__scroll-item-tag

  &__scroll-item-tag {
    font-weight: 300;
    font-size: 24px;
    line-height: 165%;
  }

  // .uniqueness-pro__scroll-item-big-text

  &__scroll-item-big-text {
    max-width: 488px;
    font-weight: 700;
    font-size: 36px;
    line-height: 145%;
    color: #fff;
    margin: 0;
    margin-bottom: 18px;
    @include tablet {
      font-size: 24px;
      margin-bottom: 8px;
    }
  }

  // .uniqueness-pro__scroll-item-desc

  &__scroll-item-desc {
    max-width: 495px;
    font-weight: 300;
    font-size: 24px;
    line-height: 165%;
    color: #fff;
    opacity: 0.75;
    margin: 0;
    margin-bottom: 54px;
    span {
      font-weight: 700;
    }
    @include tablet {
      font-size: 14px;
      margin-bottom: 36px;
    }
  }

  // .uniqueness-pro__scroll-item-link

  &__scroll-item-link {
    border-radius: 500px;
    padding: 24px 36px;
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
    text-align: center;
    width: fit-content;
    transition: 0.2s ease-in;
    @include tablet {
      font-size: 14px;
      padding: 10px;
    }
    @include hover {
      opacity: 0.7;
    }
  }

  // .uniqueness-pro__swiper

  &__swiper {
    margin-top: -1px;
    padding: 36px 0;
    display: none;

    @include tablet {
      display: block;
    }
    .swiper-slide {
      opacity: 1;
    }
    .swiper-slide.swiper-slide-active {
      .uniqueness-pro__scroll-item-upper {
        opacity: 1;
        transform: unset;
      }
    }
    .swiper-button-next::after,
    .swiper-button-prev::after {
      content: "";
      background-image: url(../img/arrow-swiper.svg);
      display: flex;
      align-items: center;
      width: 34px;
      height: 26px;
      background-repeat: no-repeat;
      justify-content: center;
    }
    .swiper-button-disabled {
      transition: 0.2s ease-in;
    }

    .swiper-button-prev::after {
      transform: rotate(-180deg);
    }
    .swiper-button-next,
    .swiper-button-prev {
      background-color: rgba($color: #fff, $alpha: 0.7);
      border-radius: 100%;
      width: 78px;
      height: 78px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .swiper-button-next {
      top: 40%;
      right: 25px;
    }
    .swiper-button-prev {
      display: none;
      left: 40px;
      transform: translateX(-50%);
    }
    .swiper-button-disabled {
      opacity: 0;
    }
    .swiper-button-next,
    .swiper-button-prev {
    }
  }
}

.event-power {
  // .event-power__wrapper

  &__wrapper {
    padding: 72px 0;
    background-color: #f7f7f7;
    @include tablet {
      padding: 36px 0;
    }
  }

  // .event-power__content

  &__content {
    display: flex;
    gap: 18px;
    margin-bottom: 72px;
    @include tablet {
      flex-direction: column;
      margin-bottom: 36px;
    }
  }

  // .event-power__become

  &__become {
    background: #fff;
    border-radius: 36px;
    display: flex;
    align-items: center;
    gap: 54px;
    padding: 51px 36px;
    @include tablet {
      flex-direction: column;
      gap: 18px;
      padding: 19px;
    }
    @include mobile {
      align-items: flex-start;
    }
  }

  // .event-power__become-img

  &__become-img {
  }

  // .event-power__become-text

  &__become-text {
    max-width: 416px;
    display: flex;
    flex-direction: column;
    gap: 54px;
    @include tablet {
      gap: 11px;
      padding-left: 10px;
    }
  }

  // .event-power__become-text-item

  &__become-text-item {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  // .event-power__become-text-item-name

  &__become-text-item-name {
    font-weight: 700;
    font-size: 18px;
    line-height: 145%;
    color: #000;
  }

  // .event-power__become-text-item-desc

  &__become-text-item-desc {
    font-weight: 300;
    font-size: 14px;
    line-height: 175%;
    color: #000;
    margin: 0;
    @include tablet {
      max-width: 312px;
    }
  }

  // .event-power__video

  &__video {
    display: flex;
    border-radius: 36px;
    background: #fff;
    padding: 13px;
    @include tablet {
      padding: 19px;
      justify-content: center;
    }
  }

  // .event-power__video-link

  &__video-link {
    z-index: 1;
    position: relative;
    border-radius: 36px;
    overflow: hidden;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      width: 100%;
      height: 100%;
      top: 0;
      background: rgba(252, 230, 0, 0.24);
      z-index: 1;
      @include tablet {
        background: rgba(112, 126, 236, 0.36);
      }
    }
    @include mobile {
      width: 100%;
    }
    img {
      position: relative;
      z-index: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__download {
    display: flex;
    gap: 10px;
    justify-content: space-between;
    align-items: center;
    @include tablet {
      flex-direction: column;
      gap: 0;
      padding: 0 29px;
    }
  }

  // .event-power__download-title

  &__download-title {
    font-weight: 700;
    font-size: 48px;
    line-height: 145%;
    color: #000;
    margin: 0;
    max-width: 538px;
    @include tablet {
      font-weight: 700;
      font-size: 36px;
      line-height: 145%;
      color: #000;
    }
  }

  // .event-power__download-text

  &__download-text {
    display: flex;
    flex-direction: column;
    gap: 18px;
    max-width: 524px;
    @include tablet {
      gap: 45px;
    }
  }

  // .event-power__download-desc

  &__download-desc {
    font-weight: 300;
    font-size: 14px;
    line-height: 175%;
    color: #000;
    margin: 0;
  }

  // .event-power__download-link

  &__download-link {
    border-radius: 500px;
    background-color: #fff;
    padding: 18px 26px;
    font-weight: 700;
    font-size: 14px;
    line-height: 125%;
    text-align: center;
    color: #fce600;
    width: fit-content;
    transition: 0.2s ease-in;
    @include tablet {
      font-weight: 700;
      font-size: 14px;
      line-height: 125%;
      text-align: center;
      color: #fff;
      background-color: #0419fd;
    }

    &:hover {
      color: #000;
      background-color: #fce600;
    }
  }
}

.event-promo {
  // .event-promo__wrapper
  margin-bottom: 72px;
  @include tablet {
    margin-bottom: 36px;
  }
  &__wrapper {
    padding: 72px 0;
    background-color: #f7f7f7;
    @include tablet {
      padding: 36px 0;
      background: linear-gradient(
        180deg,
        rgba(247, 247, 247, 1) 90%,
        rgba(255, 255, 255, 1) 100%
      );
    }
  }

  // .event-promo__content

  &__content {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 17px;
    @include tablet {
      grid-template-columns: 1fr;
    }
  }

  // .event-promo__content-left

  &__content-left {
    grid-column: span 2;
    display: flex;
    flex-direction: column;
    gap: 14px;
    @include tablet {
      grid-column: span 1;
    }
  }

  // .event-promo__content-left-item

  &__content-left-item {
    flex: 1;
  }

  // .event-promo__content-item

  &__content-item {
    border-radius: 36px;
    background: #fff;
    position: relative;
    padding: 36px;
  }

  // .event-promo__content-item-img

  &__content-item-img {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    border-radius: 16px;
    padding: 6px;
    top: 23px;
    right: 23px;
    img {
      max-width: 36px;
    }
  }

  // .event-promo__content-item-name

  &__content-item-name {
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    color: #000;
    margin-bottom: 12px;
    max-width: 252px;
    display: block;
  }

  // .event-promo__content-item-desc

  &__content-item-desc {
    font-weight: 300;
    font-size: 12px;
    line-height: 175%;
    color: #000;
    opacity: 0.8;
    margin: 0;
    max-width: 288px;
  }

  // .event-promo__content-item-dark

  &__content-item-dark {
    border-radius: 36px;
    background: #000;
    padding: 24px 54px;
  }

  // .event-promo__content-item-dark-desc

  &__content-item-dark-desc {
    font-weight: 700;
    font-size: 14px;
    line-height: 175%;
    color: #fff;
  }

  // .event-promo__content-middle

  &__content-middle {
    grid-column: span 3;
    border-radius: 36px;
    background: #fff;
    padding: 0 59px;
    display: flex;
    flex-direction: column;
    @include tablet {
      grid-column: span 1;
      padding-top: 36px;
    }
    img {
      @include tablet {
        max-width: 284px;
        margin: 0 auto;
        display: block;
      }
    }
  }

  // .event-promo__content-middle-text

  &__content-middle-text {
    flex: 1;
    font-weight: 500;
    font-size: 24px;
    line-height: 145%;
    color: #000;
    margin-bottom: 18px;
    padding-left: 34px;
    display: block;
    margin-top: 61px;
    padding-right: 19px;
