.become-pro {
  margin-bottom: 72px;
  @include tablet {
    margin-bottom: 36px;
  }
  // .become-pro__wrapper

  &__wrapper {
  }

  // .become-pro__content

  &__content {
    display: flex;
    justify-content: space-between;
    margin-bottom: 72px;
    @include tablet {
      margin-bottom: 36px;
      flex-direction: column;
      gap: 54px;
      align-items: center;
    }
  }

  // .become-pro__consult

  &__consult {
    display: flex;
    flex-direction: column;
  }

  // .become-pro__consult-title

  &__consult-title {
    font-weight: 500;
    font-size: 48px;
    line-height: 130%;
    color: #000;
    margin: 0;
    margin-bottom: 12px;
    max-width: 433px;
    @include tablet {
      font-weight: 500;
      font-size: 36px;
      line-height: 130%;
      color: #000;
      max-width: 433px;
    }
    @include mobile {
      max-width: 325px;
    }
  }

  // .become-pro__consult-desc

  &__consult-desc {
    display: block;
    max-width: 579px;
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 18px;
    line-height: 175%;
    color: #000;
    opacity: 0.8;
    margin: 0;
    margin-bottom: 36px;
    @include tablet {
      font-weight: 300;
      font-size: 14px;
      line-height: 165%;
      color: #000;
    }
  }

  // .become-pro__consult-link

  &__consult-link {
    border-radius: 500px;
    padding: 24px 36px;
    background: #fce600;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
    text-align: center;
    color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
    width: fit-content;
    border: 1px solid transparent;
    transition: 0.2s ease-in;
    &:hover {
      background: #fff;
      border: 1px solid #fce600;
      opacity: 0.9;
    }
  }

  // .become-pro__list

  &__list {
    display: flex;
    flex-direction: column;
    gap: 54px;
    max-width: 400px;
    counter-reset: my-counter;
    padding-left: 0;
    @include tablet {
      max-width: 575px;
    }
    @include mobile {
      max-width: unset;
      width: 100%;
    }
  }

  // .become-pro__item

  &__item {
    counter-increment: my-counter;
    position: relative;
    padding-left: 45px;
    &::after {
      content: counter(my-counter, decimal-leading-zero) ".";
      position: absolute;
      left: 0;
      top: 0;
      font-weight: 700;
      font-size: 18px;
      line-height: 175%;
      color: #000;
    }
  }
  // .become-pro__item-name

  &__item-name {
    font-weight: 700;
    font-size: 18px;
    line-height: 175%;
    color: #000;
    opacity: 0.8;
    display: block;
    margin-bottom: 8px;
  }

  // .become-pro__item-desc

  &__item-desc {
    font-weight: 300;
    font-size: 14px;
    line-height: 175%;
    color: #000;
    opacity: 0.8;
    margin: 0;
  }
}

