.hero {
  // .hero__content
  margin-bottom: 72px;
  @include small-tablet {
    margin-bottom: 54px;
  }
  &__content {
    display: flex;
    @include small-tablet {
      margin-right: -16px;
    }
  }
  .swiper-slide {
    transition: 0.5s ease;
  }

  .swiper-slide:hover {
    transform: scale(1.05); /* Увеличение слайда */
    opacity: 0.8;
  }
  // .hero__scroll

  &__scroll {
    user-select: none;
    display: flex;
    gap: 14px;
    overflow-x: auto;
    padding-bottom: 54px;
    @include small-tablet {
      padding-bottom: 36px;
      padding-right: 16px;
    }
  }

  &__swiper {
    margin-top: -15px;
    padding-top: 15px;
    margin-left: -5px;
    padding-left: 5px;
    margin-right: -5px;
    padding-right: 5px;
    padding-bottom: 54px;
    @include tablet {
      padding-bottom: 36px;
    }
    .swiper-scrollbar {
      background: #242424;
    }

    .swiper-scrollbar-drag {
      background: #fff;
    }
    .swiper-scrollbar-lock {
      display: block !important;
    }
  }
  .swiper-slide.slide-1 {
    width: 202px;
    height: 320px;
    @include small-tablet {
      height: 223px;
    }
  }

  .swiper-slide.slide-2 {
    width: 334px;
    height: 320px;
    @include small-tablet {
      height: 223px;
      width: 264px;
    }
  }

  .swiper-slide.slide-3 {
    width: 267px;
    height: 320px;
    @include small-tablet {
      height: 223px;
    }
  }

  .swiper-slide.slide-4 {
    width: 267px;
    height: 320px;
    @include small-tablet {
      height: 223px;
    }
  }

  .swiper-slide.slide-5 {
    width: 235px;
    height: 320px;
    @include small-tablet {
      height: 223px;
    }
    &.last {
      &:hover {
        all: unset;
      }
    }
  }

  // .hero__item

  &__item {
    user-select: none;
    display: flex;
    flex-direction: column;
    gap: 18px;

    position: relative;
    @include hover {
    }
    &.last {
      min-width: 233px;
      @include small-tablet {
        min-width: 202px;
      }
    }
  }

  // .hero__item-img-container

  &__item-img-container {
    pointer-events: none;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    background-size: cover;
    background-position: top;
    height: 274px;
    display: flex;
    align-items: center;
    border-radius: 36px;
    @include small-tablet {
      height: 177px;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  // .hero__item-name

  &__item-name {
    font-weight: 500;
    font-size: 22px;
    line-height: 125%;
    text-align: center;
    color: #fff;
  }

  // .hero__item-coming

  &__item-coming {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-weight: 300;
    font-size: 22px;
    line-height: 100%;
    text-align: center;
    background: linear-gradient(271deg, #e0e0e0 0%, #fff 49.5%, #e0e0e0 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  // .hero__item-future

  &__item-future {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    border-radius: 36px;

    background: #1c1c1e;
    margin-bottom: 24px;
    height: 50%;
  }

  // .hero__item-text

  &__item-text {
    height: 45%;
    font-weight: 500;
    font-size: 36px;
    line-height: 125%;
    color: #fff;
    @include small-tablet {
      font-size: 22px;
    }
  }
}
