.qr-section {
  // .qr-section__wrapper

  &__wrapper {
    padding: 90px 0;
    background-color: #fce600;
    @include tablet {
      padding: 36px 0;
    }
  }

  // .qr-section__title

  &__title {
    font-weight: 600;
    font-size: 64px;
    line-height: 125%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    text-align: center;
    color: #000;
    margin: 0 auto;
    margin-bottom: 12px;
    @include tablet {
      font-size: 24px;

      margin-bottom: 8px;
    }
  }

  // .qr-section__subtitle

  &__subtitle {
    display: block;
    width: fit-content;
    margin: 0 auto;
    margin-bottom: 54px;
    font-size: 14px;
    font-weight: 300;
    font-size: 14px;
    line-height: 165%;
    text-align: center;
    color: rgba(0, 0, 0, 0.65);
    @include tablet {
      margin-bottom: 36px;
    }
  }

  // .qr-section__code

  &__code {
    padding: 45px;
    margin: 0 auto;
    max-width: 334px;
    overflow: hidden;
    max-height: 334px;
    margin-top: 72px;
    border-radius: 36px;
    position: relative;
    background-image: url(../img/border.webp);
    background-repeat: no-repeat;
    background-size: cover;
    @include tablet {
      display: none;
    }
  }
}
