.download-app {
  // .download-app__wrapper

  &__wrapper {
    background-color: #fce600;

    background-image: url(../img/bg-lines.svg);
    background-repeat: no-repeat;
    background-size: cover;
    @include tablet {
      padding: 36px 0;
    }
  }
  &__main-img-container {
    display: flex;
    justify-content: center;
  }
  &__title-mobile {
    display: none;
    margin: 0 auto;
    font-weight: 300;
    font-size: 14px;
    line-height: 165%;
    text-align: center;
    color: #000;
    max-width: 253px;

    @include tablet {
      display: block;
    }
  }
  // .download-app__title
  &__main-img {
    max-width: 1009px;
    max-height: 568px;
    margin: 0 auto;
    display: block;
    @include tablet {
      min-width: 430px;
      max-width: 430px;
    }
    @include mobile {
      min-width: auto;
      max-width: auto;
    }
  }
  &__title {
    max-width: 803px;

    font-weight: 600;
    font-size: 48px;
    line-height: 125%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    text-align: center;
    color: #000;
    margin: 0 auto;
    margin-top: 36px;
    margin-bottom: 12px;
    @include tablet {
      font-weight: 600;
      font-size: 24px;
      line-height: 125%;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      text-align: center;
      color: #000;
    }
  }
  &__link {
    background-color: #fff;
    border-radius: 500px;
    display: flex;
    padding: 24px 36px;
    width: fit-content;
    margin: 0 auto;
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
    text-align: center;
    color: #000;
    transition: 0.2s ease-in;
    @include hover {
      opacity: 0.8;
    }
  }

  // .download-app__subtitle

  &__subtitle {
    display: block;
    margin-bottom: 54px;
    font-weight: 300;
    font-size: 20px;
    line-height: 125%;
    text-align: center;
    color: rgba(0, 0, 0, 0.65);
    @include tablet {
      font-weight: 300;
      margin: 0 auto;
      margin-bottom: 36px;
      font-size: 14px;
      line-height: 165%;
      text-align: center;
      color: rgba(0, 0, 0, 0.65);
      max-width: 305px;
    }
  }
}

.visiting {
  // .visiting__wrapper

  &__wrapper {
    transform: translateZ(0);
    backface-visibility: hidden;
    background-color: #000;
  }

  // .visiting__content

  &__content {
    display: flex;
    z-index: 1;
    position: relative;
    justify-content: space-between;
    @include tablet {
      flex-direction: column-reverse;
      justify-content: center;
      align-items: center;
      gap: 50px;
    }
  }

  // .visiting__title

  &__title {
    font-weight: 500;
    font-size: 48px;
    line-height: 145%;
    color: #fff;
    margin: 0;
    margin-bottom: 54px;
    @include tablet {
      font-weight: 500;
      font-size: 24px;
      line-height: 145%;
      text-align: center;
      color: #fff;
      margin-bottom: 8px;
    }
  }
  &__text {
    max-width: 535px;
  }
  // .visiting__desc

  &__desc {
    font-weight: 300;
    font-size: 20px;
    line-height: 145%;
    color: #fff;
    max-width: 349px;
    display: block;
    margin: 0;
    margin-bottom: 65px;
    @include tablet {
      font-weight: 300;
      font-size: 14px;
      line-height: 165%;
      text-align: center;
      color: #fff;
      margin: 0 auto;
      margin-bottom: 59px;
    }
    .c-pink {
      font-weight: 700;
      @include tablet {
        color: white;
      }
    }
  }

  // .visiting__info

  &__info {
    display: flex;
    gap: 18px;
    align-items: flex-end;
  }

  // .visiting__info-item

  &__info-item {
    height: fit-content;
    border-radius: 36px;
    padding: 28px 18px 41px 18px;
    img {
      max-width: 74px;
    }
    &.white {
      background: #1c1c1e;
    }
    &.dark {
      background: rgba(13, 13, 14, 1);
    }
  }

  // .visiting__info-item-desc

  &__info-item-desc {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #fff;
    margin: 0;
    margin-top: 26px;
    max-width: 173px;
    &.big {
      font-weight: 600;
      font-size: 18px;
      line-height: 150%;
      color: #fff;
    }
  }

  // .visiting__info-item-link

  &__info-item-link {
    width: fit-content;
    display: block;
    font-weight: 300;
    font-size: 14px;
    line-height: 150%;
    padding: 10px 24px;
    color: #000;
    background: #fce600;
    border-radius: 100px;
    margin-top: 36px;
  }
}

.search-engine {
  // .search-engine__wrapper

  &__wrapper {
    transform: translateZ(0);
    backface-visibility: hidden;
    background-color: #000;
    position: relative;

    display: flex;
    align-content: center;
    @include tablet {
      padding: 30px 0;
    }
    /*    &::after {
      content: "";
      position: absolute;
      left: 24px;
      bottom: -33px;
      width: 204px;
      height: 421px;
      filter: blur(200px);
      background: #e574f8;
    } */
  }
  .swiper-button-next::after,
  .swiper-button-prev::after {
    content: "";
    background-image: url(../img/arrow-swiper.svg);
    display: flex;
    align-items: center;
    width: 34px;
    height: 26px;
    background-repeat: no-repeat;
    justify-content: center;
  }
  .swiper-button-disabled {
    transition: 0.2s ease-in;
  }

  .swiper-button-prev::after {
    transform: rotate(-180deg);
  }
  .swiper-button-next,
  .swiper-button-prev {
    @include tablet {
      display: none;
    }
    background-color: rgba($color: #fff, $alpha: 0.7);
    border-radius: 100%;
    width: 78px;
    height: 78px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .swiper-button-next {
    right: 45px;
  }
  .swiper-button-prev {
    left: 45px;
    transform: translateX(-50%);
  }
  .swiper-button-disabled {
    opacity: 0;
  }
  .swiper-button-next,
  .swiper-button-prev {
    top: 242px;
  }
  // .search-engine__content
  .swiper-slide-img-2 {
    width: 446px;
    height: 501px;
  }
  &__content {
    width: 100%;
    display: grid;
    grid-template-columns: auto auto;
    align-content: center;
    align-items: center;
    justify-content: space-between;
    @include tablet {
      grid-template-columns: 1fr;
      justify-content: center;
    }
  }

  // .search-engine__info

  &__info {
    max-width: 555px;
  }

  // .search-engine__title

  &__title {
    font-weight: 500;
    font-size: 48px;
    line-height: 145%;
    color: #fff;
    margin: 0;
    margin-bottom: 54px;
    @include tablet {
      font-weight: 500;
      font-size: 24px;
      line-height: 145%;
      text-align: center;
      color: #fff;
      margin-bottom: 8px;
    }
  }

  // .search-engine__desc

  &__desc {
    margin: 0;
    font-weight: 300;
    font-size: 26px;
    line-height: 145%;
    color: #fff;
    max-width: 416px;
    @include tablet {
      font-weight: 300;
      font-size: 14px;
      line-height: 165%;
      text-align: center;
      color: #fff;
      margin-bottom: 54px;
    }
    .c-dark-pink {
      font-weight: 700;
    }
  }
  .swiper-scrollbar-engine {
    display: none;
    @include tablet {
      display: block;
    }
    position: relative;
    top: 125px;
    border-radius: 5000px;
    background: #242424;
    height: 4px;
  }

  .swiper-scrollbar-drag {
    background: #fff;
  }
  .swiper-scrollbar-drag {
    background-color: #fff;
  }
  // .search-engine__swiper-container

  &__swiper-container {
    max-width: 445px;
    position: relative;
    @include tablet {
      min-width: 100%;
      padding-bottom: 55px;
    }
  }

  // .search-engine__swiper

  &__swiper {
    .swiper-slide.swiper-slide-active {
      opacity: 1;
    }
    .swiper-slide {
      mix-blend-mode: luminosity;
      opacity: 0.15;
    }
    .swiper-slide-padding {
      padding: 8px;
      overflow: hidden;
      border-radius: 36px;
      min-height: 501px;
      overflow: hidden;
      background-color: #ff0e7a;
      background-repeat: no-repeat;
      background-size: cover;
      display: flex;
      &.slide-2 {
        background-color: #e574f8;
        .search-engine__slide-upper-nav {
          background: rgba(229, 116, 248, 0.25);
        }
        .search-engine__slide-upper-link {
          background: #e574f8;
        }
      }
    }
    .swiper-slide {
    }
  }

  // .search-engine__slide-wrapper
  &__slide-content {
    display: flex;
    flex-direction: column;
    background-image: url(../img/search-slide-1.webp);
    background-repeat: no-repeat;
    background-size: cover;
    padding: 16px;
    border-radius: 36px;
    height: 483px;
    overflow: hidden;
    &.slide-2 {
      background-image: url(../img/search-engine-slide-2.png);
      .swiper-slide-padding {
        background-color: #e574f8;
      }
    }
  }
  &__slide-wrapper {
    min-height: 100%;
    width: 100%;
  }

  // .search-engine__slide-upper-nav

  &__slide-upper-nav {
    border-radius: 36px;
    display: flex;
    flex-direction: column;
    background: rgba(255, 14, 122, 0.25);
    max-width: 396px;

    padding: 23px;
  }

  // .search-engine__slide-upper-title

  &__slide-upper-title {
    font-weight: 700;
    font-size: 16px;
    line-height: 110%;
    color: #fff;
    margin-bottom: 3px;
  }

  // .search-engine__slide-upper-desc

  &__slide-upper-desc {
    font-weight: 400;
    font-size: 14px;
    line-height: 110%;
    color: #fff;
    margin-bottom: 14px;
  }

  // .search-engine__slide-upper-links

  &__slide-upper-links {
    display: flex;
    gap: 7px;
    flex-wrap: wrap;
  }

  // .search-engine__slide-upper-link

  &__slide-upper-link {
    background: #ff0e7a;
    font-weight: 400;
    font-size: 12px;
    line-height: 110%;
    color: #fff;
    display: block;
    padding: 6px 14px;
    border-radius: 500px;
  }

  // .search-engine__slide-bottom-nav

  &__slide-bottom-nav {
    margin: 0 auto;
    margin-top: auto;
    max-width: 237px;
  }

  // .search-engine__slide-btn

  &__slide-btn {
  }

  // .search-engine__slide-title

  &__slide-title {
    margin-top: 36px;
    display: block;
    font-weight: 700;
    font-size: 32px;
    line-height: 135%;
    color: #fff;
    margin: 0;
    margin-top: 36px;
    @include tablet {
      max-width: 256px;
      font-weight: 700;
      font-size: 24px;
      line-height: 135%;
      text-align: center;
      color: #fff;
      margin: 0 auto;
      margin-top: 16px;
    }
  }
}
