.eco {
  // .eco__wrapper

  &__wrapper {
    background-color: #fce600;
    display: flex;
    align-items: center;
    @include mobile {
      padding: 36px 0;
    }
  }

  // .eco__content

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 36px;
    @include tablet {
      grid-template-columns: 1fr;
      gap: 54px;
    }
  }

  // .eco__info

  &__info {
    display: flex;
    flex-direction: column;
    gap: 53px;
    @include mobile {
      gap: 36px;
      width: 100%;
      margin: 0 auto;
    }
  }

  // .eco__info-upper

  &__info-upper {
    border-radius: 36px;
    background: rgba(2, 2, 2, 0.04);
    display: flex;
    flex-direction: column;
    gap: 26px;
    padding: 74px;
    @include mobile {
      padding: 36px 28px;
    }
  }

  // .eco__info-title

  &__info-title {
    font-weight: 500;
    font-size: 48px;
    line-height: 130%;
    color: #000;
    margin: 0;
    @include tablet {
      font-weight: 500;
      font-size: 36px;
      line-height: 130%;
      color: #000;
      margin-bottom: 12px;
      max-width: 285px;
    }
  }

  // .eco__info-desc

  &__info-desc {
    font-weight: 300;
    font-size: 24px;
    line-height: 145%;
    color: #000;
    margin: 0 auto;
    @include tablet {
      font-weight: 300;
      font-size: 18px;
      line-height: 165%;
      color: #000;
      text-align: left;
      max-width: 285px;
      margin: unset;
    }
  }

  // .eco__bottom

  &__bottom {
  }

  // .eco__bottom-desc

  &__bottom-desc {
    font-weight: 500;
    font-size: 48px;
    line-height: 145%;
    color: #fff;
    margin: 0 auto;
    max-width: 535px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      right: 0;
      background-image: url(../img/eco-lines-1.svg);
      width: 238px;
      height: 115px;
      background-size: cover;
      transform: translateY(50%);
      @include tablet {
        background-image: url(../img/eco-lines-2.svg);
        width: 105px;
        height: 82px;
      }
    }
    @include tablet {
      font-weight: 500;
      font-size: 36px;
      line-height: 145%;
      color: #fff;
      max-width: 300px;
      margin: unset;
      padding-left: 28px;
    }
  }

  // .eco__right

  &__right {
    overflow: hidden;
    padding: 95px 75px;
    border-radius: 36px;
    background: #0419fd;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      width: 538px;
      height: 684px;
      bottom: -30%;
      right: -30%;
      background-image: url(../img/eco-icon.svg);
      opacity: 0.12;
      @include mobile {
        width: 306px;
        height: 390px;
        background-size: cover;
        right: -64px;
        bottom: -94px;
      }
    }
    @include mobile {
      padding: 36px 28px;
      width: 100%;
      margin: 0 auto;
    }
  }

  // .eco__right-title

  &__right-title {
    max-width: 340px;
    font-weight: 700;
    font-size: 48px;
    line-height: 130%;
    color: #fff;
    margin: 0;
    margin-bottom: 26px;
    @include tablet {
      font-weight: 700;
      font-size: 36px;
      line-height: 130%;
      color: #fff;
      margin-bottom: 12px;
      max-width: 248px;
    }
    .c-yellow {
      font-weight: 800;
    }
  }

  // .eco__right-desc

  &__right-desc {
    font-weight: 300;
    font-size: 36px;
    line-height: 150%;
    color: #fff;
    margin: 0;
    margin-bottom: 72px;
    display: block;
    max-width: 417px;
    @include tablet {
      font-weight: 300;
      font-size: 18px;
      line-height: 165%;
      color: #fff;
      max-width: 230px;
      margin-bottom: 66px;
    }
  }

  // .eco__right-link

  &__right-link {
    border-radius: 36px;
    padding: 24px 18px;
    display: block;
    background: #fff;
    font-weight: 300;
    font-size: 18px;
    line-height: 110%;
    color: #000;
    width: fit-content;
    @include tablet {
      font-weight: 700;
      font-size: 14px;
      line-height: 110%;
      color: #000;
      padding: 10px;
    }
  }
}

.uniqueness {
  margin-top: -1px;
  margin-bottom: 72px;
  @include tablet {
    margin-bottom: 36px;
    margin-right: -16px;
  } // .uniqueness__wrapper

  &__wrapper {
    overflow: hidden;
    background: #fff;
  }

  // .uniqueness__scroll
  &__swiper {
    margin-top: -1px;
    padding: 36px 0;
    display: none;

    @include tablet {
      display: block;
    }
    .swiper-slide {
      opacity: 1;
    }
    .swiper-slide.swiper-slide-active {
      .uniqueness__scroll-item-upper {
        opacity: 1;
        transform: unset;
      }
    }
    .swiper-button-next::after,
    .swiper-button-prev::after {
      content: "";
      background-image: url(../img/arrow-swiper.svg);
      display: flex;
      align-items: center;
      width: 34px;
      height: 26px;
      background-repeat: no-repeat;
      justify-content: center;
    }
    .swiper-button-disabled {
      transition: 0.2s ease-in;
    }

    .swiper-button-prev::after {
      transform: rotate(-180deg);
    }
    .swiper-button-next,
    .swiper-button-prev {
      background-color: rgba($color: #fff, $alpha: 0.7);
      border-radius: 100%;
      width: 78px;
      height: 78px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .swiper-button-next {
      top: 40%;
      right: 25px;
    }
    .swiper-button-prev {
      display: none;
      left: 40px;
      transform: translateX(-50%);
    }
    .swiper-button-disabled {
      opacity: 0;
    }
    .swiper-button-next,
    .swiper-button-prev {
    }
  }
  &__scroll {
    @include tablet {
      display: none;
    }
    padding: 72px 0;
    @include tablet {
      padding: 36px 0;
    }
    display: flex;
    gap: 100px;
    padding-bottom: 54px;
    &::-webkit-scrollbar {
      border-radius: 5000px;
      background: rgba(#102048, 0.14);
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 5000px;
      background-color: rgba($color: #102048, $alpha: 1);
    }
  }

  // .uniqueness__scroll-item

  &__scroll-item {
    flex: 0 0 100%;
    img {
      width: 100%;
    }
  }

  // .uniqueness__scroll-item-upper

  &__scroll-item-upper {
    display: flex;
    flex-direction: column;
    gap: 18px;

    margin-bottom: 130px;
    @include tablet {
      transition: 0.2s ease-in;
      transform: translateX(-300px);
      opacity: 0;
      margin-bottom: 36px;
      gap: 8px;
    }
  }

  // .uniqueness__scroll-item-title

  &__scroll-item-title {
    font-weight: 700;
    font-size: 64px;
    line-height: 110%;
    text-align: center;
    color: #000;
    margin: 0 auto;
    @include tablet {
      font-weight: 700;
      font-size: 30px;
      line-height: 110%;
      text-align: center;
      color: #000;
    }
  }

  // .uniqueness__scroll-item-subtitle

  &__scroll-item-subtitle {
    font-weight: 300;
    font-size: 24px;
    line-height: 165%;
    text-align: center;
    color: #000;
    opacity: 0.75;
    @include tablet {
      max-width: 289px;
      margin: 0 auto;
      font-weight: 300;
      font-size: 14px;
      line-height: 165%;
      text-align: center;
      color: #000;
    }
  }

  // .uniqueness__scroll-item-content

  &__scroll-item-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 72px;
    align-items: center;
    @include tablet {
      grid-template-columns: 1fr;
      gap: 12px;
    }
  }

  // .uniqueness__scroll-item-img

  &__scroll-item-img {
    position: relative;
    max-width: 547px;
    margin: 0 auto;
    @include mobile {
      max-width: 100%;
      margin: 0 auto;
    }
  }

  // .uniqueness__scroll-item-text

  &__scroll-item-text {
    display: flex;
    flex-direction: column;
    @include tablet {
      max-width: 366px;
      margin: 0 auto;
    }
  }

  // .uniqueness__scroll-item-tag

  &__scroll-item-tag {
    font-weight: 300;
    font-size: 24px;
    line-height: 165%;
  }

  // .uniqueness__scroll-item-big-text

  &__scroll-item-big-text {
    max-width: 488px;
    font-weight: 700;
    font-size: 36px;
    line-height: 145%;
    color: #000;
    margin: 0;
    margin-bottom: 18px;
    @include tablet {
      font-size: 24px;
      margin-bottom: 8px;
    }
  }

  // .uniqueness__scroll-item-desc

  &__scroll-item-desc {
    max-width: 495px;
    font-weight: 300;
    font-size: 24px;
    line-height: 165%;
    color: #000;
    opacity: 0.75;
    margin: 0;
    margin-bottom: 54px;
    span {
      font-weight: 700;
    }
    @include tablet {
      font-size: 14px;
      margin-bottom: 36px;
    }
  }

  // .uniqueness__scroll-item-link

  &__scroll-item-link {
    border-radius: 500px;
    padding: 24px 36px;
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
    text-align: center;
    width: fit-content;
    transition: 0.2s ease-in;
    @include tablet {
      font-size: 14px;
      padding: 10px;
    }
    @include hover {
      opacity: 0.7;
    }
  }
}
