.footer {
  padding: 54px 0 24px 0;
  @include small-tablet {
    padding-bottom: 8px;
  }
  // .footer__content
  &__logo {
    max-width: 118px;
    height: 55px;
    @include small-tablet {
      margin-bottom: 10px;
      max-width: 86px;
      height: 40px;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
  }

  // .footer__content-upper

  &__content-upper {
    display: grid;
    grid-template-columns: auto 1fr 1fr;
    gap: 50px;
    margin-bottom: 43px;
    @include small-tablet {
      grid-template-columns: 1fr;
      gap: 24px;
    }
  }

  // .footer__content-upper-fcopy

  &__content-upper-fcopy {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  // .footer__content-upper-fcopy-name

  &__content-upper-fcopy-name {
    font-weight: 500;
    font-size: 13px;
    line-height: 150%;
    color: #eeeeef;
    span {
      font-weight: 500;
      font-size: 13px;
      line-height: 150%;
      background: linear-gradient(
        310deg,
        #e32900 0%,
        #c6e216 61.47%,
        #d808fa 100%
      );
      margin-left: 16px;
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }

  // .footer__content-upper-fcopy-copyr

  &__content-upper-fcopy-copyr {
    font-weight: 300;
    font-size: 13px;
    line-height: 150%;
    color: #eeeeef;
  }

  // .footer__content-upper-fcopy-loc

  &__content-upper-fcopy-loc {
    font-weight: 300;
    font-size: 13px;
    line-height: 150%;
    color: #7e8083;
  }

  // .footer__content-upper-links

  &__content-upper-links {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  // .footer__content-upper-link

  &__content-upper-link {
    font-weight: 300;
    font-size: 16px;
    line-height: 150%;
    color: #eeeeef;
  }

  // .footer__content-links

  &__content-links {
    margin-bottom: 43px;
    border-top: 1px solid rgba($color: #fff, $alpha: 0.1);
    padding-left: 170px;
    @include small-tablet {
      padding-left: 0;
      border: unset;
    }
  }

  // .footer__content-links-list

  &__content-links-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    @include small-tablet {
      grid-template-columns: 1fr;
      gap: 28px;
    }
  }

  // .footer__content-links-item

  &__content-links-item {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }

  // .footer__content-links-item-name

  &__content-links-item-name {
    margin-bottom: 6px;
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 16px;
    line-height: 125%;

    color: #fff;
  }

  // .footer__content-links-item-link

  &__content-links-item-link {
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 16px;
    line-height: 125%;
    color: #fff;
    opacity: 0.65;
    transition: 0.2s ease-in;
    @include hover {
      opacity: 1;
    }
  }

  // .footer__copyright

  &__copyright {
    margin-left: 170px;
    border-top: 1px solid rgba(168, 170, 174, 0.32);
    padding-top: 8px;
    span {
      font-weight: 300;
      font-size: 16px;
      line-height: 150%;
      color: #eeeeef;
    }
    @include small-tablet {
      margin-left: unset;

      font-size: 12px;
    }
  }
}
