.faq {
  // .faq__wrapper

  &__wrapper {
    background-color: #fafafa;
    padding: 72px 0;
    @include tablet {
      padding: 36px 0;
    }
  }

  // .faq__title

  &__title {
    font-weight: 500;
    font-size: 48px;
    line-height: 130%;
    text-align: center;
    color: #000;
    margin: 0 auto;
    max-width: 677px;
    margin-bottom: 72px;
    @include tablet {
      font-weight: 500;
      font-size: 36px;
      line-height: 130%;
      text-align: center;
      color: #000;
      margin-bottom: 54px;
    }
  }

  // .faq__list

  &__list {
    max-width: 800px;
    margin: 0 auto;
  }
  &__item {
    margin-bottom: 18px;
    border-radius: 5px;
  }
  &__question {
    position: relative;
    z-index: 1;
    color: #000;
    background: #fff;
    &.active {
      box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.08);
    }
    width: 100%;
    border: none;
    font-size: 18px;
    text-align: left;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    border-radius: 16px;
    padding: 27px 26px 27px 36px;
    display: flex;
    transition: 0.2s ease-in;
    @include tablet {
      padding: 17px 36px;
    }
  }
  &__icon {
    font-size: 20px;
    color: #fce600;
    background-color: rgba(252, 230, 0, 0.2);
    min-width: 30px;
    min-height: 30px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__answer {
    border-radius: 0 0 16px 16px;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease;
    padding: 0 45px;
    background: #fff;
    position: relative;
    top: -20px;
    z-index: 0;
    @include tablet {
      padding: 0 14px 0 36px;
    }
    p {
      font-weight: 400;
      font-size: 14px;
      line-height: 175%;
      color: #000;
      padding-top: 27px;
      @include tablet {
      }
    }
  }
  &__link {
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
    text-align: center;
    color: #fff;
    display: flex;
    justify-content: center;
    width: fit-content;
    border-radius: 500px;
    padding: 24px 77px;
    background: #081cf4;
    transition: 0.2s ease-in;
    margin: 0 auto;
    margin-top: 54px;
    &:hover {
      opacity: 0.7;
    }
    @include mobile {
      width: 100%;
      margin-top: 36px;
    }
  }
}
