html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
* {
  &::-webkit-scrollbar {
    appearance: none;
    height: 4px;
    border-radius: 5000px;

    background-color: #3c3c3d;
    opacity: 0.14;
  }
  &::-webkit-scrollbar-thumb {
    background: #fff;
    border-radius: 5000px;
  }
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}
.footer {
  background-color: #000;
}
.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  background-color: #000;
  scroll-behavior: auto;
  &.white {
    background-color: #fff;
    .header__logo {
      display: none;
    }
    .header__logo.white {
      display: block;
    }
    .header .menu__link {
      font-weight: 300;
      font-size: 16px;
      line-height: 125%;
      text-align: center;
      color: #000;
    }
    .options-container {
      background-color: #fff;
    }
    .custom-option-text {
      color: #000;
    }
    .custom-select .selected-text {
      color: #000;
    }
    .custom-select .arrow svg path {
      stroke: #000;
    }
    .header .burger {
      color: #000;
    }
    .header__user {
      background-color: #fce600;
      border: 1px solid transparent;
      transition: 0.2s ease-in;
      @include small-tablet {
        background-color: #fff;
      }
      &:hover {
        opacity: 0.8;
      }
    }
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  @include tablet {
    overflow: hidden;
  }
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.bg-pink {
  background: #ff0e7a;
}

.custom-select {
  position: relative;
  display: inline-block;
  cursor: pointer;
  opacity: 0.65;
  transition: 0.2s ease-in;
  @include hover {
    opacity: 1;
  }
  @include small-tablet {
    margin-left: auto;
    margin-right: 15px;
  }
}

.selected-option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.2s ease-in;
}
.custom-select.open .arrow {
  transform: rotate(90deg);
}
.options-container {
  background-color: #000;
  opacity: 0;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  z-index: 10;
  transition: 0.2s ease-in;
  pointer-events: none;
}

.custom-option {
  position: relative;
  display: block;
  cursor: pointer;
  opacity: 0.65;
  transition: 0.2s ease-in;
  @include hover {
    opacity: 1;
  }
}

.custom-option-text {
  color: #fff;
  display: block;
  padding-top: 20px;
}
.selected-text {
  color: #fff;
}
.custom-option:hover {
  color: #fff;
}

.custom-select input[type="radio"]:checked + .custom-option-text {
}
.custom-select input[type="radio"] {
  position: absolute;
  top: 0;
  left: 0;
  appearance: none;
}
.custom-select.open .options-container {
  opacity: 1;
  pointer-events: all;
}

.user-account {
  min-width: 202px;
  @include small-tablet {
    display: none;
  }
  &.mobile {
    display: none;
    @include small-tablet {
      display: flex;
      position: absolute;
      bottom: 44px;
      left: 50%;
      transform: translateX(-50%);
      margin: 0;
    }
  }
  border-radius: 500px;
  padding: 10px;
  background: #fff;
  display: flex;
  align-items: center;
  gap: 8px;
  margin-left: 26px;
  transition: 0.2s ease-in;
  @include hover {
    background: #fce600;
  }
  &:active {
    background: #fce600;
    opacity: 0.65;
  }
}
.play-video {
  font-weight: 600;
  font-size: 14px;
  line-height: 145%;
  color: #000;
  display: flex;
  gap: 10px;
  align-items: center;
  border-radius: 500px;
  padding: 8px 6px;
  background: #fff;
  transition: 0.2s ease-in;
  @include hover {
    background: #fce600;
  }
  &:active {
    background: #fce600;
    opacity: 0.65;
  }
  @include tablet {
    font-weight: 600;
    font-size: 12px;
    line-height: 145%;
    color: #000;
  }
}
.underline {
  position: relative;
  &::after {
    position: absolute;
    width: 85%;
    border-radius: 10px;
    transform: translateX(-50%);
    left: 50%;

    bottom: -10px;
    height: 6px;
    background-color: #fff;
    content: "";
  }
}

.showcase-bg-name {
  position: relative;
  z-index: 1;
  &::after {
    content: "";
    position: absolute;
    width: calc(100% + 8px);
    height: calc(100% + 8px);
    z-index: -1;
    border-radius: 14px;
    background-color: #fce600;
    transform: translate(-50%, -50%) rotate(-1deg);
    left: 50%;
    top: 50%;
  }
}

.c-white {
  color: #fff;
}
.items-wrap {
  gap: 4px;
  position: relative;
  display: flex;
  overflow: hidden;
  user-select: none;
}

.items {
  flex-shrink: 0;
  display: flex;
  justify-content: space-around;
  min-width: 100%;
}

.marquee {
  animation: scroll 35s linear infinite;
}
.reverce {
  animation-direction: reverse;
}

@keyframes scroll {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(calc(-100% - 20px));
  }
}

