  }
}

.hover-yellow-in-white {
  transition: 0.2s ease-in;
  @include hover {
    background-color: #fff;
  }
}
.hover-white-in-yellow {
  transition: 0.2s ease-in;
  @include hover {
    background-color: #fce600;
  }
}
.swipe-section {
  position: relative;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

.swipe-section .panel {
  width: 100%;
  height: 100%;
  position: absolute;
}

.double-scroll-content {
  .vertical-scroll-item {
    transition: 0.3s ease-in;
  }
  .vertical-scroll-item:nth-child(2) {
    margin-top: 30px;
    opacity: 0;
    @include tablet {
      opacity: 1;
    }
  }
  &.active {
    .vertical-scroll-item:nth-child(1) {
      opacity: 0;
      margin-top: -100%;
    }
    .vertical-scroll-item:nth-child(2) {
      opacity: 1;
      margin-top: -100%;
    }
  }
}
.double-scroll {
  will-change: transform;
}
.double-scroll-content-second {
  .vertical-scroll-item {
    transition: 0.3s ease-in;
  }
  .vertical-scroll-item:nth-child(2) {
    margin-top: 30px;
    opacity: 0;
    @include tablet {
      opacity: 1;
    }
  }
  &.active {
    .vertical-scroll-item:nth-child(1) {
      opacity: 0;
      margin-top: -100%;
    }
    .vertical-scroll-item:nth-child(2) {
      opacity: 1;
      margin-top: -100%;
    }
  }
}

.vertical-section {
  will-change: transform;
}
.scroll-section .item {
  will-change: transform;
}

.scroll-section .item {
  position: sticky;
  top: 0;
  height: 100vh;
  @include tablet {
    position: static;

    height: auto;
  }
}

.pin-spacer-out {
  position: sticky !important;
  top: 0 !important;
  padding: 0 !important;

  .vertical-scroll-in {
    transform: unset !important;
  }
  .vertical-scroll-in-second {
    transform: unset !important;
  }
}
