.marquee-section {
  // .marquee-section__wrapper

  &__wrapper {
    margin-bottom: 72px;
    @include tablet {
      margin-bottom: 36px;
    }
  }

  // .marquee-section__title

  &__title {
    font-weight: 600;
    font-size: 48px;
    line-height: 125%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    text-align: center;
    color: #fff;
    margin: 0;
    margin-bottom: 18px;
    @include tablet {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 24px;
      line-height: 125%;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      text-align: center;
      color: #fff;
      margin-bottom: 16px;
    }
  }

  // .marquee-section__subtitle

  &__subtitle {
    font-weight: 300;
    font-size: 22px;
    letter-spacing: -0.01em;
    display: block;
    text-align: center;
    color: rgba(255, 255, 255, 0.75);
    margin-bottom: 36px;
    @include tablet {
      font-family: var(--font-family);
      font-weight: 300;
      font-size: 18px;
      letter-spacing: -0.01em;
      text-align: center;
      color: rgba(255, 255, 255, 0.7);
      margin-bottom: 24px;
    }
  }
  // .marquee-section__scroll

  &__scroll {
    display: flex;
    flex-direction: column;
    gap: 4px;
    @include tablet {
      margin: 0 -16px;
    }
    .items-wrap {
      margin-top: -100px;
      padding-top: 100px;

      margin-bottom: -100px;
      padding-bottom: 100px;
    }
    .items {
      gap: 4px;
    }
    .item {
      @include tablet{
        padding: 24px;
      }
      transition: 0.2s ease-in;
      @include tablet {
        font-size: 16px;
      }
      &.blue {
        &::after {
          z-index: -1;
          content: "";
          position: absolute;
          left: 50%;
          top: 50%;
          width: 100%;
          height: 100%;
          border-radius: 20px;
          filter: blur(35px);
          background: #4fe3fd;
          transform: translate(-50%, -50%);
        }
        background: radial-gradient(
            37.44% 34.93% at 19.13% 0%,
            #096bc7 0%,
            rgba(9, 107, 199, 0) 100%
          ),
          radial-gradient(
            189.84% 65.78% at 63.92% 100%,
            #4fe3fd 0%,
            rgba(79, 227, 253, 0) 100%
          ),
          #3193ef;
        position: relative;
      }
      &.pink {
        &::after {
          z-index: -1;
          content: "";
          position: absolute;
          left: 50%;
          top: 50%;
          width: 100%;
          height: 100%;
          border-radius: 20px;
          filter: blur(35px);
          background: #ff0eb1;
          max-height: 57px;
          transform: translate(-50%, -50%);
        }
        background: radial-gradient(
            68.03% 68.03% at 48.09% 100%,
            #ff36d9 0%,
            rgba(255, 54, 217, 0) 100%
          ),
          #ff0eb1;
        position: relative;
      }
      &.yellow {
        &::after {
          z-index: -1;
          content: "";
          position: absolute;
          left: 50%;
          top: 50%;
          width: 100%;
          height: 100%;
          border-radius: 20px;
          filter: blur(35px);
          background: #fce600;
          transform: translate(-50%, -50%);
        }
        color: #0a0a0a;
        position: relative;
        background: radial-gradient(
            37.25% 72.79% at 50.49% 0%,
            #dec814 0%,
            rgba(222, 200, 20, 0) 100%
          ),
          radial-gradient(
            37.27% 64.71% at 37.27% 100%,
            #ffff1e 0%,
            rgba(255, 255, 30, 0) 100%
          ),
          #fce600;
      }
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 20px;
      box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.25);
      font-family: var(--second-family);
      font-weight: 600;
      font-size: 18px;
      letter-spacing: -0.01em;
      text-align: center;
      color: #fff;
      border-radius: 24px;
      padding: 32px;
      background: radial-gradient(
          68.9% 95.39% at 44.02% 0%,
          rgba(255, 255, 255, 0.08) 0%,
          rgba(255, 255, 255, 0) 100%
        ),
        radial-gradient(
          51.96% 21.84% at 49.98% 0%,
          rgba(255, 255, 255, 0.08) 0%,
          rgba(255, 255, 255, 0) 100%
        ),
        radial-gradient(
          24.47% 5.75% at 50% 0%,
          rgba(255, 255, 255, 0.08) 0%,
          rgba(255, 255, 255, 0) 100%
        ),
        radial-gradient(
          53.83% 12.64% at 49.96% 100%,
          rgba(255, 255, 255, 0.08) 0%,
          rgba(255, 255, 255, 0) 100%
        ),
        rgba(255, 255, 255, 0.04);
    }
  }
}
