.container {
}
.header {
  .user-account {

      background-color: #fff;
    &:hover{
      background-color: #fce600;
    }

  }

  position: relative;
  z-index: 10;
  margin-bottom: 72px;
  margin-top: 36px;
  padding: 22px 40px;
  @include small-tablet {
    padding: 16px 0;
    margin-bottom: 36px;
  }
  &__navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    @include small-tablet {
      max-width: 75px;
    }
  }
  .menu {
    margin-left: auto;
    margin-right: 60px;
    @include small-tablet {
      margin-left: unset;
      margin-right: unset;
      position: fixed;
      background-color: #fce600;
      bottom: 0;
      top: 0;
      right: -100%;
      width: 100%;
      z-index: 9;
      padding-top: 69px;
      align-items: flex-start;
      display: flex;
      transition: right 0.2s ease-in;
      &--active {
        right: 0;

        .menu__link {
          color: #000;
          opacity: 1;
        }
      }
    }
    &__link {
      opacity: 0.65;
      font-weight: 300;
      font-size: 16px;
      line-height: 125%;
      text-align: center;
      color: #fff;
      transition: 0.2s ease-in;
      @include hover {
        opacity: 1;
      }
    }
    &__list {
      display: flex;
      column-gap: 30px;
      @include small-tablet {
        flex-direction: column;
        row-gap: 36px;
        text-align: left;
        margin-left: 36px;
      }
    }
    &__item {
      @include small-tablet {
        display: flex;
        text-align: left;
      }
    }
  }
  .burger {
    &--active {
      z-index: 10;
    }
    img {
      display: none;
    }
    @include burger;
    display: none;
    @include small-tablet {
      display: block;
    }
  }
}

