.showcase {
  // .showcase__content
  margin-bottom: 72px;
  @include tablet {
    margin-bottom: 36px;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 18px;
    @include tablet {
      grid-template-columns: repeat(2, 1fr);
      gap: 16px;
    }
  }

  // .showcase__item

  &__item {
    overflow: hidden;
    padding: 38px;
    border-radius: 36px;
    @include small-desktop {
      padding: 20px;
    }
    @include tablet {
      padding: 21px 18px;
      min-height: 214px;
    }
    &.blue {
      padding: 0;
      position: relative;
      @include tablet {
        flex-direction: column;
        padding-top: 22px;
      }
      & > img {
        max-width: 342px;
        max-height: 352px;
      }
      grid-column: span 2;
      display: flex;
      padding-left: 38px;
      align-items: center;
      justify-content: space-between;
      background: #0419fd;
      .showcase__name {
        max-width: 520px;
        font-weight: 300;
        font-size: 48px;
        line-height: 145%;
        color: #fff;
        @include tablet {
          font-weight: 300;
          font-size: 32px;
          line-height: 145%;
          color: #fff;
          max-width: 300px;
        }
      }
    }
    &.pink {
      position: relative;

      img {
        bottom: 25px;
        right: 33px;
        position: absolute;
        @include tablet {
          max-width: 60px;
          bottom: 6px;
          right: 6px;
        }
      }
      @include tablet {
        padding: 21px 21px 65px;
      }
      background: #ff0e7a;
      .showcase__name {
        font-weight: 600;
        font-size: 30px;
        line-height: 125%;
        color: #fff;
        position: relative;
        @include tablet {
          font-weight: 600;
          font-size: 16px;
          line-height: 125%;
          color: #fff;
        }

        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 14px;
          height: 18px;
          transform: translate(-100%, -100%);
          background-image: url(../img/text-quotation-start.svg);
          background-size: cover;
          background-repeat: no-repeat;
          @include tablet {
            width: 10px;
            height: 10px;
          }
        }
        &::after {
          @include tablet {
            width: 10px;
            height: 10px;
          }
          content: "";
          position: absolute;
          right: -16px;
          bottom: 0;
          background-size: cover;
          background-repeat: no-repeat;
          width: 14px;
          height: 18px;
          transform: translateX(100%);
          background-image: url(../img/text-quotation-end.svg);
        }
      }
    }
    &.video {
      display: flex;
      align-items: center;
      justify-content: center;
      background-image: url(../img/showcase-video-bg.webp);
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      flex-direction: column;
      gap: 37px;
      @include tablet {
        gap: 18px;
      }
      .showcase__name {
        font-weight: 600;
        font-size: 40px;
        line-height: 125%;
        color: #fff;
        @include tablet {
          font-weight: 600;
          font-size: 16px;
          line-height: 125%;
          color: #fff;
          max-width: 132px;
        }
        .showcase-bg-name {
          color: #000;
        }
      }
    }
    &.yellow {
      display: flex;
      flex-direction: column;
      text-align: center;
      overflow: hidden;
      grid-column: 3 / 4;
      grid-row: 1 / 3;
      position: relative;
      @include tablet {
        grid-column: span 2;
        grid-row: span 2;
        min-height: 400px;
      }
      & > img {
        z-index: 0;
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        @include tablet {
          bottom: 0px;
          height: 180px;
          object-position: top;
          object-fit: cover;
        }
      }
      background: #fce600;
      .showcase__name {
        font-weight: 400;
        font-size: 18px;
        line-height: 125%;
        color: #000;
        @include tablet {
          margin-bottom: 8px;
          font-size: 12px;
        }
      }
      .showcase__sub-name {
        font-weight: 700;
        font-size: 24px;
        line-height: 125%;
        color: #000;
        margin-bottom: 30px;
        display: block;
        @include tablet {
          font-size: 16px;
          margin-bottom: 18px;
        }
      }
    }
  }

  // .showcase__name

  &__name {
  }

  // .showcase__list

  &__list {
    margin-top: 37px;
    display: flex;
    flex-direction: column;
    gap: 26px;
    .c-yellow {
      font-weight: 700;
    }
    @include tablet {
      margin-top: 17px;
      gap: 13px;
    }
  }
  &__list-item {
    font-weight: 400;
    font-size: 18px;
    line-height: 125%;
    color: #fff;
    position: relative;
    display: flex;
    align-items: center;
    &::before {
      margin-right: 10px;
      margin-top: 10px;
      content: "*";
      font-weight: 400;
      font-size: 24px;
      color: #fff;
      @include tablet {
        margin-right: 5px;
        margin-top: 5px;
        font-weight: 400;
        font-size: 18px;
        line-height: 125%;
        color: #fff;
      }
    }
    @include tablet {
      font-weight: 400;
      font-size: 12px;
      line-height: 125%;
      color: #fff;
    }
  }
  // .showcase__play

  &__play {
  }

  // .showcase__sub-name

  &__sub-name {
  }

  // .showcase__item-content

  &__item-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 8px;
    justify-items: center;
    max-width: 245px;
    margin: 0 auto;
    @include tablet {
      gap: 10px;
    }
  }

  // .showcase__item-content-container

  &__item-content-container {
    display: flex;
    font-weight: 600;
    font-size: 14px;
    line-height: 125%;
    text-align: center;
    color: rgba(0, 0, 0, 0.7);
    padding: 20px;
    background: rgba(255, 255, 255, 0.42);
    border-radius: 36px;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 3px;
    @include tablet {
      flex-direction: row;
      gap: 0;
      color: rgba(0, 0, 0);
      font-size: 14px;
      line-height: 125%;
      text-align: center;
    }
    &:last-child {
      position: relative;
      font-weight: 600;
      font-size: 18px;
      text-align: left;
      width: 100%;
      line-height: 125%;
      color: #fce600;
      background-color: #000000;
      grid-column: span 2;
      max-width: 100%;
      @include tablet {
        font-size: 14px;
      }
      &::after {
        content: "";
        position: absolute;
        top: 23px;
        right: 23px;
        width: 19px;
        height: 19px;
        background-image: url(../img/showcase-last-end.svg);
        background-repeat: no-repeat;
        background-size: cover;
      }
    }
  }

  // .showcase__item-content-text

  &__item-content-text {
    max-width: 150px;
  }

  // .showcase__item-bottom-text

  &__item-bottom-text {
    position: relative;
    z-index: 1;
    font-weight: 600;
    font-size: 12px;
    line-height: 145%;
    text-align: center;
    color: #fff;
    display: block;
    margin: auto auto 0 auto;
    max-width: 217px;
  }
}
