.advantages {
  // .advantages__wrapper

  &__wrapper {
    background-color: #fff;
    padding: 72px 0;
    position: relative;
    background: radial-gradient(circle, rgba(243,243,243,1) 0%, rgba(255,255,255,1) 100%);
  }

  // .advantages__content

  &__content {
    position: relative;
    z-index: 1;

    gap: 18px;

    display: grid;
    grid-template-columns: repeat(4, 1fr);
    @include tablet {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  // .advantages__item

  &__item {
    max-height: 380px;
    &.last {
      position: relative;
      img {
        position: absolute;
        right: 0;
        bottom: 16px;
      }
      background: #000;
      .advantages__item-desc {
        font-weight: 700;
        font-size: 24px;
        line-height: 175%;
        color: #fce600;
        @include small-desktop {
          font-size: 20px;
        }
      }
    }
    @include tablet {
      max-width: 332px;
      padding: 22px 45px;
    }
    &:nth-child(1) {
      grid-row: 1/3;
      min-height: 100%;
    }
    &:nth-child(2) {
      grid-row: 1/2;
    }
    &:nth-child(3) {
      min-height: 100%;
      grid-row: 1/3;
    }
    &:nth-child(4) {
      grid-row: 1/2;
    }
    &:nth-child(5) {
      grid-row: 3/4;
    }
    &:nth-child(6) {
      grid-row: 2/4;
      min-height: 100%;
    }
    &:nth-child(7) {
      grid-row: 3/4;
    }
    &:nth-child(8) {
      min-height: 100%;
      grid-row: 2/4;
    }
    display: flex;

    justify-content: center;
    flex-direction: column;
    border-radius: 36px;
    background: #fff;
    padding: 54px 36px;
  }

  // .advantages__item-title

  &__item-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 150%;
    color: #000;
    margin: 0;
    margin-bottom: 8px;
    @include tablet {
      font-size: 24px;
    }
  }

  // .advantages__item-desc

  &__item-desc {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    color: #000;
    margin: 0;
  }
}
